import './ChartWorldWideCovid.css'
import { Line } from 'react-chartjs-2';
import { useState, useEffect } from 'react'
import axios from 'axios';

function chooseYear(choose) {
    if(choose === "1")
        return 7
    else if(choose === "2")
        return 14
    else if(choose === "3")
        return 30
    else if(choose === "4")
        return 90
    else
        return 0
}

function ChartWorldWideCovid() {
    const [chart, setChart] = useState({})
    
    const Chart = (selectYear) => {
        let arrCases = []
        let arrCalCases = []
        let arrDeaths = []
        let arrCalDeaths = []
        let arrLabel = []

        axios.get("https://disease.sh/v3/covid-19/historical/all?lastdays=" + selectYear)
            .then(res => {
                arrLabel = Object.keys(res.data.cases);
                arrCases = Object.values(res.data.cases);
                arrDeaths = Object.values(res.data.deaths);
                //cal cases
                arrCalCases = arrCases.map((item, i) => {
                    if (i === (arrCases.length - 1)) {
                        return item - arrCases[i - 1]
                    }
                    return arrCases[i + 1] - item
                })

                //cal deaths
                arrCalDeaths = arrDeaths.map((item, i) => {
                    if (i === (arrDeaths.length - 1)) {
                        return item - arrDeaths[i - 1]
                    }
                    return arrDeaths[i + 1] - item
                })

                setChart({
                    labels: arrLabel,
                    datasets: [
                        {
                            label: 'ติดเชื้อ',
                            data: arrCalCases,
                            borderColor: '#FF0000',
                            backgroundColor: '#FF0000',
                            fill: false,
                            cubicInterpolationMode: 'monotone',
                            tension: 0.4
                        },
                        {
                            label: 'เสียชีวิต',
                            data: arrCalDeaths,
                            borderColor: '#000000',
                            backgroundColor: '#000000',
                            fill: false,
                            cubicInterpolationMode: 'monotone',
                            tension: 0.4
                        }
                    ]
                })
            })
            .catch(err => {
                console.log(err);
            })
    }

    useEffect(() => {
        Chart(7);
    }, []);

    return (
        <div className="ChartWorldWideCovid">
            <Line data={chart}
                options={{
                    responsive: true,
                    plugins: {
                        title: {
                            display: true,
                            text: 'Worldwide covid chart'
                        },
                    },
                    interaction: {
                        intersect: false,
                    },
                    scales: {
                        x: {
                            display: true,
                            title: {
                                display: true,
                                text: 'วันที่'
                            }
                        },
                        y: {
                            display: true,
                            title: {
                                display: true,
                                text: 'ยอดคน'
                            }
                        }
                    }
                }}
            />
            <select className="retrospect-world-wide" onChange={(e) => {
                const selectYear = e.target.value
                Chart(chooseYear(selectYear))
            }}>
                <option value="1">7 วันที่ผ่านมา</option>
                <option value="2">14 วันที่ผ่านมา</option>
                <option value="3">1 เดือนผ่านมา</option>
                <option value="4">3 เดือนผ่านมา</option>
            </select>
        </div>
    )
}

export default ChartWorldWideCovid;